body {
  margin: 0;
}

.body-lock {
  position: fixed;
  overflow-y: scroll;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.page-header {
  width: 100%;
  min-height: 825px;
  background-image: url("../img/head_bike_desc.png");
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header {
    background-image: url("../img/head_bike_desc@2x.png");
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .page-header {
    background: none;
    min-height: fit-content;
  }
}

.main {
  position: relative;
  padding-top: 256px;
  display: flex;
  width: 878px;
  margin: 0 auto;
  justify-content: space-between;
}
.main h1 {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 46px;
  font-weight: 700;
  line-height: 64px;
  letter-spacing: -0.005em;
  color: #ffffff;
