}
.main p {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 300;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.8);
  width: 381px;
}

@media (max-width: 1023px) {
  .main {
    padding-top: 0;
    flex-direction: column;
    width: 100%;
  }
  .main .main__wrapper {
    padding-top: 25px;
    background-image: url("../img/head_bike_desc.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: -50px;
    height: 315px;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
