    background-image: url("../img/head_bike_desc@2x.png");
    background-size: cover;
  }
}
@media (max-width: 1023px) {
  .main img {
    margin-left: 21px;
  }
}
@media (max-width: 1023px) {
  .main h1 {
    font-size: 30px;
    width: 525px;
    margin: 0 auto;
    margin-top: 75px;
    margin-bottom: 10px;
    text-align: center;
  }
}
@media (max-width: 1023px) {
  .main p {
    font-size: 18px;
    text-align: center;
    width: 435px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .main .main__wrapper {
    min-height: 390px;
    background-image: url("../img/head_bike_mobile.png");
    background-size: cover;
