.main-nav .main-nav__btn {
  display: none;
}
.main-nav .main-nav__list {
  list-style: none;
  width: 873.5px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 21px 24px 9px 19px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.main-nav a {
  text-decoration: none;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.7);
}
.main-nav a:hover,
.main-nav a:focus {
  color: white;
  padding-bottom: 15px;
  border-bottom: 1px solid white;
}

@media (max-width: 1023px) {
  .main-nav .main-nav__list {
    flex-direction: column;
    justify-content: start;
    width: 100%;
    min-height: 568px;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    margin-top: 100px;
  }
  .main-nav .main-nav__list--opened {
    height: 100vh;
    overflow: auto;
  }
  .main-nav .main-nav__list--opened::-webkit-scrollbar {
    display: none;
  }
  .main-nav .main-nav__list--closed {
    display: none;
  }
  .main-nav .main-nav__item {
    width: 100%;
    min-height: 60px;
    background-color: #f6f6f6;
    display: flex;
    align-items: center;
    padding-left: 16px;
    margin-bottom: 30px;
    position: relative;
  }
  .main-nav .main-nav__item::after {
    content: "";
    position: absolute;
    width: 7.4px;
    height: 12px;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-size: contain;
    top: 23px;
    right: 25px;
  }
  .main-nav a {
    color: #48494d;
    font-size: 20px;
    font-weight: 400;
  }
  .main-nav .main-nav--closed {
    display: none;
  }
  .main-nav .main-nav__btn--cross {
    display: block;
    width: 22px;
    height: 22px;
    padding: 0;
    position: absolute;
    border: none;
    background-color: #ffffff;
    background-image: url("../img/cross.svg");
    background-size: cover;
    top: 15px;
    right: -3px;
    cursor: pointer;
  }
  .main-nav .main-nav__btn--burger {
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    border: none;
    background-image: url("../img/burger.svg");
    background-size: contain;
    top: 0;
    right: 0;
    cursor: pointer;
    z-index: 10;
  }
}
.form {
