  width: 263px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;
  counter-increment: my-counter;
  position: relative;
}
.facts__item::before {
  content: "0" counter(my-counter);
  position: absolute;
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  color: #0ad9c6;
  top: -51px;
